<template>
    <view class="things_user_rank">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">个人等级</block></cu-custom>
        <!-- 等级卡片 -->
        <view class="things_width_full flex justify-center align-center rank_card">
            <view class="bg-img bg-mask card_box" style="background-image: url('./../../../../static/images/things/user/rank/car_bg.png');">
                <view class="flex justify-start align-center card_top">
                    <image class="rank_icon" src="./../../../../static/images/global/rank.png" mode="widthFix" />
                    <view class="rank_name">当前等级：{{gradeInfo.userType}}</view>
                </view>
                <view class="flex justify-between align-center card_bottom">
                    <view class="update_tip">升级可享受更多权益</view>
                    <button class="cu-btn update_btn" :disabled="buttonType" v-if="gradeInfo.is_upgrade_grade === 1" @tap="linkToUpgrade">升级</button>
                </view>
            </view>
        </view>
        <!-- 等级内容 -->
        <view class="things_width_full rank_box">
            <view class="rank_title">会员等级说明</view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { getGradeInfo_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
    import { grabCarorder_format,grabCarOrderTip_format } from '@/utils/filters.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            gradeInfo: {},//用户等级信息
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.base({name:'refreshPage',value:false});
            }
        }
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.getGradeInfo();
        },
        /**跳转到升级页面
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUpgrade(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/identity/list/index`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**获取用户等级信息
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getGradeInfo(e) {
            getGradeInfo_api({},that).then(res=>{
                that.gradeInfo = res;
            }).catch(err=>{
                console.log(err);
            })
        }
    },
    onLoad: function(options) {
        that = this;
        that.getGradeInfo();
    }
}
</script>
<style>
    page {
        width: 100vw;
        height: 100vh;
        background-color: #FFF;
    }
</style>
<style lang="scss" scoped>
    @import './index';
</style>
